    width: 85vw;
    max-width: 320px;
  }
  
  .sidebar-header {
    padding: 1rem 0.75rem;
    min-height: 60px;
  }
  
  .sidebar-logo {
    width: 36px;
    height: 36px;
  }
  
  .sidebar-logo-icon {
    width: 18px;
    height: 18px;
  }
  
  .sidebar-title {
    font-size: 0.9rem;
  }
  
  .sidebar-subtitle {
    font-size: 0.7rem;
  }
  
  .close-btn {
    min-width: 32px;
    min-height: 32px;
  }
  
  .close-btn svg {
    width: 18px;
    height: 18px;
  }
  
  .sidebar-content {
    padding: 1rem 0.75rem;
  }
  
  .sidebar .chat-item {
    padding: 0.75rem;
    margin-bottom: 0.5rem;
  }
  
  .sidebar .chat-title {
    font-size: 0.8rem;
  }
  
  .sidebar .chat-message {
    font-size: 0.7rem;
  }
  
  .sidebar .chat-timestamp {
    font-size: 0.65rem;
  }
  
  .close-btn {
    min-width: 32px;
    min-height: 32px;
  }
  
  .unread-badge {
    min-width: 1rem;
    height: 1rem;
    font-size: 0.6rem;
    padding: 0.125rem 0.25rem;
  }
}

/* ÌÑ∞Ïπò ÏµúÏ†ÅÌôî */
@media (max-width: 768px) {
  .chat-item {
    min-height: 3.5rem;
    display: flex;
    align-items: center;
  }
  
  .chat-item:active {
    background-color: #f3f4f6;
    transform: scale(0.98);
  }
  
  .hamburger-btn:active {
    transform: translateY(0) scale(0.98);
  }
  
  .new-chat-icon-btn:active {
    transform: scale(0.95);
  }
  
  .start-chat-btn:active {
    transform: scale(0.98);
  }
}

/* ÌÖçÏä§Ìä∏ Ïò§Î≤ÑÌîåÎ°úÏö∞ Ï≤òÎ¶¨ */
.truncate {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.line-clamp-2 {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

/* Ïä§ÌÅ¨Î°§ ÏµúÏ†ÅÌôî */
.chats-list {
  max-height: calc(100vh - 200px);
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}

/* ÏÇ¨Ïù¥ÎìúÎ∞î Ïä§ÌÅ¨Î°§ ÏµúÏ†ÅÌôî */
.sidebar-content {
  max-height: calc(100vh - 80px);
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}

/* Ïä§ÌÅ¨Î°§Î∞î Ïä§ÌÉÄÏùºÎßÅ */
.sidebar::-webkit-scrollbar,
.sidebar-content::-webkit-scrollbar {
  width: 6px;
}

.sidebar::-webkit-scrollbar-track,
.sidebar-content::-webkit-scrollbar-track {
  background: #f3f4f6;
}

.sidebar::-webkit-scrollbar-thumb,
.sidebar-content::-webkit-scrollbar-thumb {
  background: #667eea;
  border-radius: 3px;
}

.sidebar::-webkit-scrollbar-thumb:hover,
.sidebar-content::-webkit-scrollbar-thumb:hover {
  background: #5a67d8;
}

.chats-list::-webkit-scrollbar {
  width: 4px;
}

.chats-list::-webkit-scrollbar-track {
  background: transparent;
}

.chats-list::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.1);
  border-radius: 2px;
}

.chats-list::-webkit-scrollbar-thumb:hover {
  background: rgba(0, 0, 0, 0.2);
}

/* ÏÇ¨Ïù¥ÎìúÎ∞î Ïï†ÎãàÎ©îÏù¥ÏÖò Í∞úÏÑ† */
.sidebar {
  will-change: transform;
}

.sidebar-overlay {
  will-change: opacity;
}

/* ÌÇ§Î≥¥Îìú Ï†ëÍ∑ºÏÑ± */
.hamburger-btn:focus,
.new-chat-icon-btn:focus,
.start-chat-btn:focus,
.close-btn:focus {
  outline: 2px solid #667eea;
  outline-offset: 2px;
}

/* Îã§ÌÅ¨ Î™®Îìú ÏßÄÏõê - Î∞ùÏùÄ ÌÖåÎßà Ïú†ÏßÄ */
@media (prefers-color-scheme: dark) {
  .sidebar {
    background: #ffffff;
  }
  
  .sidebar-header {
    background: #667eea;
  }
  
  .sidebar-content {
    background: linear-gradient(180deg, #fafbfc 0%, #ffffff 100%);
  }
  
  .sidebar .chat-item {
    background: white;
    border-color: #e5e7eb;
  }
  
  .sidebar .chat-item:hover {
    background: white;
    border-color: #c7d2fe;
  }
  
  .sidebar .chat-title {
    color: #1f2937;
  }
  
  .sidebar .chat-message {
    color: #9ca3af;
  }
  
  .sidebar .chat-timestamp {
    color: #667eea;
  }
  
  .close-btn {
    background: rgba(255, 255, 255, 0.2);
    border: none;
    color: white;
  }
  
  .close-btn:hover {
    background: rgba(255, 255, 255, 0.3);
  }
}
